import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import Cookies from "js-cookie";
import { ProgressSpinner } from "primereact/progressspinner";

function App() {
  const navigate = useNavigate();
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [message, setMessage] = useState("");
  const [loader, setLoader] = useState(true);

  useEffect(() => {
    // Check if the cookie exists
    const isLoggedIn = Cookies.get("loggedIn");

    // If the cookie exists and its value is 'true', redirect to another page
    if (isLoggedIn === "true") {
      setLoader(false);
      navigate("/admin/dashboard");
    } else {
      setLoader(false);
      navigate("/admin");
    }
  }, []);

  const handleSubmit = async (e) => {
    e.preventDefault();

    // Send the username and password to the backend for validation
    try {
      // const response = await fetch("/api/login", {
      const response = await fetch("/api/login", {
        // const response = await fetch("/api/login", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ username, password }),
      });

      if (response.ok) {
        setMessage("Login successful!");
        Cookies.set("loggedIn", "true", { expires: 1 });
        navigate("/admin/dashboard");
      } else {
        setMessage("Login failed. Please check your credentials.");
      }
    } catch (error) {
      console.error("Error:", error);
    }
  };

  return (
    <div className="flex h-screen bg-gray-100">
      {loader ? (
        // Display a loading spinner while checking the cookie
        <div className="card">
          <ProgressSpinner
            style={{ width: "50px", height: "50px" }}
            strokeWidth="8"
            fill="var(--surface-ground)"
            animationDuration=".5s"
          />
        </div>
      ) : (
        <div className="m-auto p-10 bg-white rounded-lg shadow-lg max-w-md">
          <div className="text-center mb-6">
            <img
              src="quiz-image.png"
              alt="Quiz"
              className="w-32 mx-auto rounded-full"
            />
            <h2 className="text-2xl mt-2">Login</h2>
          </div>
          <form onSubmit={handleSubmit}>
            <div className="mb-4">
              <label htmlFor="username" className="block text-gray-700">
                Username
              </label>
              <input
                type="text"
                id="username"
                className="w-full p-2 border rounded"
                placeholder="Enter your username"
                value={username}
                onChange={(e) => setUsername(e.target.value)}
                required
              />
            </div>
            <div className="mb-4">
              <label htmlFor="password" className="block text-gray-700">
                Password
              </label>
              <input
                type="password"
                id="password"
                className="w-full p-2 border rounded"
                placeholder="Enter your password"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                required
              />
            </div>
            <div className="text-center">
              <button
                type="submit"
                className="bg-blue-500 text-white px-4 py-2 rounded hover:bg-blue-600"
              >
                Log In
              </button>
            </div>
            {message && (
              <div className="text-red-500 mt-2 text-center">{message}</div>
            )}
          </form>
        </div>
      )}
    </div>
  );
}

export default App;
