import React, { useEffect, useState } from "react";
import { TabView, TabPanel } from "primereact/tabview";
import { DataTable } from "primereact/datatable";
import { Column } from "primereact/column";
import { Button } from "primereact/button";
import axios from "axios";

import UploadFile from "../../components/UploadFile/UploadFile";
import Spinner from "../../components/Spinner";

const Dashboard = () => {
  const [data, setData] = useState([]);
  const [activeIndex, setActiveIndex] = useState(0);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    setLoading(true);
    axios
      .get("/api/upload/fetchContent") // Replace with your API endpoint
      // .get("/api/upload/fetchContent") // Replace with your API endpoint
      .then((response) => {
        // Handle the successful response and update the state
        setLoading(false);
        setData(response.data);
      })
      .catch((error) => {
        // Handle any errors here
        setLoading(false);
        console.error("Error fetching data:", error);
      });
  }, []);
  const handleEdit = (rowData) => {
    // Implement your edit logic here
    console.log("Edit clicked for row:", rowData);
  };

  const handleDelete = (rowData) => {
    // Implement your delete logic here
    // console.log("rowData ", rowData);
    setLoading(true);
    console.log("Delete clicked for row:", rowData);
    axios
      .delete(`/api/upload/delete?id=${rowData.id}`)
      // .delete(`/api/upload/delete?id=${rowData.id}`)
      .then((result) => {
        setLoading(false);
        setData(data.filter((item) => item.id !== rowData.id));
      })
      .catch((error) => {
        setLoading(false);
        console.log(error);
      });
  };
  const handleTabChange = (e) => {
    setLoading(true);
    axios
      .get(" /api/upload/fetchContent") // Replace with your API endpoint
      // .get("/api/upload/fetchContent") // Replace with your API endpoint
      .then((response) => {
        // console.log(response);
        setLoading(false);
        // Handle the successful response and update the state
        setData(response.data);
      })
      .catch((error) => {
        setLoading(false);
        // Handle any errors here
        console.error("Error fetching data:", error);
      });

    const newIndex = e.index;
    // console.log(`Tab changed to index ${newIndex}`);

    // Perform actions or set state based on the tab change
    // For example, you can update the activeIndex state:
    setActiveIndex(newIndex);
  };

  const bodyTemplate = (rowData) => {
    // console.log("rowData", rowData);
    return (
      <div>
        {rowData.message.split("\r\n").map((line, index) => (
          <React.Fragment key={index}>
            {line}
            <br />
          </React.Fragment>
        ))}
      </div>
    );
  };

  return (
    // <div className="flex flex-col text-center h-screen justify-center ">
    //   <h1 className=" font-bold">Upload Content</h1>

    //   <UploadFile />
    // </div>
    <div className="card p-10">
      <TabView onTabChange={handleTabChange} activeIndex={activeIndex}>
        {/* football quiz content here => */}
        <TabPanel header="Football Quiz Content">
          <DataTable value={data.footballContent}>
            <Column field="datetime" header="Date" className=" w-32" />
            <Column field="time" header="Time" />
            <Column field="message" header="message" body={bodyTemplate} />
            <Column field="correct_option" header="Correct Option" />
            
          </DataTable>
        </TabPanel>
        {/* goal alert content => */}
        <TabPanel header="Goal Alert Content">
          <DataTable value={data.goalContent}>
            <Column field="datetime" header="Date" className=" w-32" />
            <Column field="type" header="Type" />
            <Column field="message" header="message" body={bodyTemplate} />
           
          </DataTable>
        </TabPanel>
        {/* npfl quiz content => */}
        
        <TabPanel header="Upload Content">
          <UploadFile />
        </TabPanel>
      </TabView>
      {loading && <Spinner />}
    </div>
  );
};

export default Dashboard;
